import react,{useEffect,useState} from 'react';
import {useParams} from 'react-router-dom';

const Profile=()=>{
    const [userProfile,setProfile] = useState(null)
    
    const {userid} = useParams()
    const [showfollow,setShowFollow] = useState(!JSON.parse(localStorage.getItem("user")).following.includes(userid))
    useEffect(()=>{
       fetch('/profile/user/'+userid,{
           headers:{
               "Authorization":"Bearer "+localStorage.getItem("jwt")
           }
       }).then(res=>res.json())
       .then(result=>{
           setProfile(result)
       })
    },[])

    const followUser = ()=>{
        fetch('/profile/follow',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem('jwt')
            },
            body:JSON.stringify({
                followId:userid
            })
        }).then(res=>res.json())
        .then(data=>{
            localStorage.setItem("user",JSON.stringify(data))
            setProfile((prevState)=>{
                return {
                    ...prevState,
                    user:{
                        ...prevState.user,
                        followers:[...prevState.user.followers,data._id]
                       }
                }
            })
            setShowFollow(false);
        })
    }

    const unfollowUser = ()=>{
        fetch('/profile/unfollow',{
            method:"put",
            headers:{
                "Content-Type":"application/json",
                "Authorization":"Bearer "+localStorage.getItem('jwt')
            },
            body:JSON.stringify({
                unfollowId:userid
            })
        }).then(res=>res.json())
        .then(data=>{
            localStorage.setItem("user",JSON.stringify(data))
            setProfile((prevState)=>{
            const newFollower = prevState.user.followers.filter(item=>item != data._id )
                return {
                    ...prevState,
                    user:{
                        ...prevState.user,
                        followers:newFollower
                       }
                }
            })
            setShowFollow(true);
        })
    }

    return(
        <>
       {userProfile ?
    <div style={{maxWidth:"550px",margin:"0px auto"}}>
        <div style={{
            display:"flex",
            justifyContent:"space-around",
            margin:"20px 0px",
            borderBottom:"1px solid grey"
        }}>
            <div>
                <img style={{width:"160px",height:"160px",borderRadius:"80px"}}
                src={userProfile.user.pic}
                ></img>
            </div>
            <div>
                
            <h4>{userProfile.user.name}</h4>
            <h5>{userProfile.user.email}</h5>
                <div style={{display:"flex",justifyContent:"space-around",width:"108%"}}>
                <h6>{userProfile.posts.length} posts</h6>
                <h6>{userProfile.user.followers.length} followers</h6>
                <h6>{userProfile.user.following.length} following</h6>
                {showfollow?
                   <button style={{
                       margin:"10px"
                   }} className="btn waves-effect waves-light #64b5f6 blue darken-1"
                    onClick={()=>followUser()}
                    >
                        Follow
                    </button>
                    : 
                    <button
                    style={{
                        margin:"10px"
                    }}
                    className="btn waves-effect waves-light #64b5f6 blue darken-1"
                    onClick={()=>unfollowUser()}
                    >
                        UnFollow
                    </button>
                    }
                </div>
            </div>
        </div>
        <div className="gallery">
            {
                userProfile.posts.map(pic=>{
                    return(
                        <img className="pic" key={pic._id} src={pic.image} alt={pic.title}/>
                    )
                })
            }
        </div>
    </div>
    : <h2>loading...!</h2>} 
    </>
    )
}

export default Profile;